@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+Display:wght@100&display=swap);
body,
body * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Noto Sans Display", sans-serif;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background: blanchedalmond;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(248, 222, 183);
}

.App {
  display: grid;
  place-items: center;
  width: 100vw;
  height: 100vh;
  background-image: url(/sqlchat2/static/media/4065770.87e20846.png);
}

.ChatBox_chatBar__3DMWP {
  grid-area: chatbox;
  background-color: rgba(1, 13, 19, 0.1);
  padding: 10px 30px;
  overflow: auto;
}

.ChatBox_chatBar__3DMWP ul {
  list-style-type: none;
  display: flex;
  flex-flow: column nowrap;
}

.ChatBox_sent__2HGMR,
.ChatBox_received__1hg27 {
  overflow: hidden;
  background-color: rgba(250, 235, 215, 0.8);
  padding: 20px;
  margin: 5px 0;
  border-radius: 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  max-width: 70%;
  font-weight: bold;
}

.ChatBox_received__1hg27 {
  background-color: rgba(0, 0, 0, 0.418);
  color: blanchedalmond;
  position: relative;
}

.ChatBox_sent__2HGMR {
  place-self: flex-end;
}

.ChatBox_time__5Mh6p {
  display: none;
}
.ChatBox_timeShow__1yX8o {
  display: block;
  text-align: end;
  font-weight: normal;
  font-style: italic;
}

.ChatInput_chatInput__2Nu07 {
  grid-area: chatform;
}

.ChatInput_chatInput__2Nu07 * {
  height: 100%;
  margin: 0;
  padding: 0;
  border: none;
}

.ChatInput_chatInput__2Nu07 form {
  display: grid;
  grid-template-columns: 8fr 2fr;
  grid-template-areas: "input send";
}

.ChatInput_chatInput__2Nu07 input[type="text"] {
  grid-area: input;
  resize: none;
  outline: none;
  padding: 5px 15px;
  font-size: 20px;
  font-weight: bold;
}
.ChatInput_chatInput__2Nu07 input[type="submit"] {
  grid-area: send;
  font-size: 20px;
  font-weight: bold;
}

.SideBar_sideBar__2j2RH {
  display: flex;
  flex-direction: column;
  place-items: center;
  grid-area: sidebar;
  height: 100%;
  background-color: rgba(1, 13, 19, 0.5);
  overflow: hidden;
}

.SideBar_contactWrap__SD8PI {
  overflow: auto;
  height: 90%;
  width: 100%;
  padding: 10px;
}

.SideBar_contact__2rjAC {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  background-color: transparent;
  border: none;
  width: 90%;
  height: 75px;
  margin: 5px;
  padding: 10px;
  border-radius: 5px;
  color: blanchedalmond;
  font-weight: bold;
  font-size: 20px;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

.SideBar_contactForm__2pnK8 {
  height: 10%;
  width: 100%;
}

.SideBar_contactForm__2pnK8 * {
  font-size: 20px;
  font-weight: bold;
  border: none;
  outline: none;
}

.SideBar_contactForm__2pnK8 input[type="submit"] {
  width: 40%;
  height: 100%;
}

.SideBar_contactForm__2pnK8 input[type="text"] {
  width: 60%;
  height: 100%;
}

.SideBar_contact__2rjAC:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.SideBar_clicked__JrMIf {
  background-color: rgba(0, 0, 0, 0.1);
}
.SideBar_clicked__JrMIf:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.SideBar_contact__2rjAC img {
  grid-area: pic;
  width: 60px;
  border-radius: 20%;
}

.SideBar_details__3WYkN {
  margin: 10px;
  text-align: left;
}

.Chat_chat__B2QoL {
  display: grid;
  grid-template-columns: 3fr 7fr;
  grid-template-rows: 35fr 5fr;
  grid-template-areas:
    "sidebar chatbox"
    "chatform chatform";
  width: 90vw;
  height: 90vh;
  border-radius: 20px;
  overflow: hidden;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.Error_container__1Fwvs * {
  color: blanchedalmond;
  font-weight: bold;
  font-size: xx-large;
}
.Error_container__1Fwvs {
  position: absolute;
  z-index: 100;
  display: grid;
  place-items: center;
  width: clamp(300px, 60vw, 800px);
  height: 50vh;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}

.Error_container__1Fwvs button {
  background: transparent;
  border: 2px solid blanchedalmond;
  border-radius: 10px;
  padding: 10px;
  width: 100px;
  cursor: pointer;
}

.Error_container__1Fwvs p {
  padding: 10px;
  text-align: center;
}

.Login_containerLog__2x0J4,
.Login_containerSub__2fA6r,
.Login_container__20jU1 {
  display: flex;
  flex-flow: nowrap column;
  justify-content: center;
  place-items: center;
  padding: 30px 0;
  min-height: 300px;
  width: clamp(300px, 50vw, 600px);
  border-radius: 20px;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background-color: rgba(1, 13, 19, 0.05);
}

.Login_containerSub__2fA6r {
  height: 40vh;
}

.Login_h2__EwRY- {
